//library
import React, { useState, useRef, useEffect } from "react";
import { useNavigate } from "react-router-dom";

import styled from "styled-components";

//component
import SelectBox from "../Utility/SelectBox";
import ImgUpload from "./ImgUpload";
import Footer from "../Footer/Footer";

//custom hooks
import useAxios from "../../hooks/useAxios";
import usePrompt from "../../hooks/usePrompt";

//etc
import { Section, Input, Textarea } from "../Style/InputStyle";
import { uploadCategoryList } from "../../data/CatecoryList";

function UploadForm({ userId, edit, editData, productId }) {
  const nav = useNavigate();

  const [imgDelete, setImgDelete] = useState([]); //edit   
  const [image, setImage] = useState([]); // 
  const [category, setCategory] = useState(""); // 
  const [writeCheck, setWriteCheck] = useState(true);

  // ref
  const titleRef = useRef();
  const priceRef = useRef();
  const countRef = useRef();
  const descriptionRef = useRef();

  //     
  const { connectServer: imageDelete } = useAxios(
    "/api/product/delImgEditPage"
  );

  //    
  const { connectServer: editProduct } = useAxios("/api/product/edit");

  //  DB 
  const { connectServer: writeProduct } = useAxios("/api/product/write");

  //    ,  
  usePrompt(" ?", writeCheck);

  useEffect(() => {
    const titleName = document.getElementsByTagName("title")[0];

    //edit     
    if (edit) {
      setImage(editData.image);
      setCategory(editData.category);

      titleRef.current.value = editData.title;
      priceRef.current.value = editData.price;
      countRef.current.value = editData.count;
      descriptionRef.current.value = editData.description;

      titleName.innerHTML = ` `;
    } else {
      titleName.innerHTML = ` `;
    }
  }, [edit, editData]);

  const maxLengthCheck = (e) => {
    //    
    e.target.value = e.target.value.replace(/[^0-9.]/g, "");

    //  
    if (e.target.id === "price") {
      if (e.target.value.length > e.target.maxLength) {
        return (e.target.value = e.target.value.slice(0, e.target.maxLength));
      }
    }

    if (e.target.id === "count") {
      if (e.target.value.length > e.target.maxLength) {
        return (e.target.value = e.target.value.slice(0, e.target.maxLength));
      }
    }
  };

  const onWrite = (e) => {
    e.preventDefault();
    if (image.length < 1) {
      return alert(" .");
    }

    if (titleRef.current.value.length < 2) {
      titleRef.current.focus();
      return alert(" 2  .");
    }

    if (priceRef.current.value.length < 1) {
      priceRef.current.focus();
      return alert(" .");
    }

    if (countRef.current.value.length < 1) {
      countRef.current.focus();
      return alert(" .");
    }

    if (category === "") {
      return alert(" .");
    }

    if (descriptionRef.current.value.length < 10) {
      descriptionRef.current.focus();
      return alert(" 10  .");
    }

    if (window.confirm(" ?")) {
      uploadData();
    }
  };

  const uploadData = async () => {
    const data = {
      writer: userId,
      title: titleRef.current.value,
      price: priceRef.current.value,
      description: descriptionRef.current.value,
      count: countRef.current.value,
      image: image,
      category: category,
    };

    try {
      if (edit) {
        // if (imgDelete.length > 0) {
        //   imageDelete(imgDelete);
        // }
        editProduct({ ...data, id: productId });
        alert(" ");
      }

      if (edit === undefined) {
        writeProduct(data);
        alert(" ");
      }
    } catch (err) {
      alert(" ");
    }

    setWriteCheck(false);
  };

  //usePrompt true        
  //,      state false  state   
  useEffect(() => {
    if (!writeCheck) {
      nav(edit ? `/product/${productId}` : "/");
    }
  }, [productId, writeCheck, nav, edit]);

  return (
    <div className="page">
      <Section>
        <div>
           <div>{`${image.length} / 12`}</div>
        </div>

        <ImgUpload
          setImage={setImage}
          setImgDelete={setImgDelete}
          edit={edit}
          editImg={editData}
        />
      </Section>

      <hr />
      <Section>
        <div></div>
        <Input
          inputType="title"
          id="title"
          placeholder="2  ."
          maxLength={40}
          ref={titleRef}
        />
        {/* <span>{`${} / 40`}</span> */}
      </Section>

      <hr />

      <Section>
        <div></div>
        <Input
          inputType="price"
          type="number"
          id="price"
          placeholder=" ."
          ref={priceRef}
          min="1"
          maxLength="8"
          onInput={maxLengthCheck}
        />
        <label></label>
      </Section>

      <hr />

      <Section>
        <div> </div>
        <Input
          inputType="count"
          type="number"
          id="count"
          placeholder=" 99"
          ref={countRef}
          maxLength="2"
          onInput={maxLengthCheck}
        />
        <label></label>
      </Section>

      <hr />

      <Section>
        <div></div>
        <SelectBox
          data={uploadCategoryList}
          setData={setCategory}
          edit={edit}
          initData={edit && editData.category}
        />
      </Section>

      <hr />

      <Section>
        <div></div>
        <Textarea
          id="description"
          placeholder="10  ."
          maxLength={500}
          ref={descriptionRef}
        />
        {/* <span>{`${} / 500`}</span> */}
      </Section>

      <hr />

      <Section />

      <Footer>
        <SubmitBtn>
          <button onClick={onWrite}>{edit ? "" : ""} </button>
        </SubmitBtn>
      </Footer>
    </div>
  );
}

const SubmitBtn = styled.div`
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: center;

  button {
    margin-right: 2rem;
    font-size: 1.5rem;
    border: none;
    border-radius: 5px;
    padding: 0.5rem;
    background-color: var(--red_transparency);
    color: white;
    transition: 0.5s;

    &:hover {
      cursor: pointer;
      background-color: var(--red);
    }
  }
`;

export default React.memo(UploadForm);
