import React, { useEffect, useState, useRef } from "react";
import { useLocation } from "react-router";
import { useNavigate } from "react-router-dom";
import { LoadingOutlined } from "@ant-design/icons";
import styled from "styled-components";

import PaymentBtn from "../../Components/Product/PaymentBtn";
import Carousel1 from "../../Components/Utility/Carousel1";
import CheckOutProduct from "./CheckOutProduct";
import CheckOutInput from "./CheckOutInput";
import FadeAnimation from "../../Components/Utility/Animation/FadeAnimation";
import SlideAnimation from "../../Components/Utility/Animation/SlideAnimation";

import useAxios from "../../hooks/useAxios";

function CheckOut({ userId }) {
  const nav = useNavigate();
  const { state } = useLocation(); //, ,   
  const [loading, setLoading] = useState(false);
  const [searchAddress, setSearchAddress] = useState("");

  const { connectServer: userSuccessBuy } = useAxios("/api/user/successBuy"); //  
  const { connectServer: removeCart } = useAxios("/api/user/removeCart"); //       
  const { connectServer: productSuccessBuy } = useAxios(
    "/api/product/successBuy"
  ); // 

  const { connectServer: addBuyer } = useAxios("/api/user/addBuyer"); //      

  const nameRef = useRef();
  const phoneRef = useRef();
  const addressRef = useRef();
  const reqRef = useRef();

  useEffect(() => {
    if (state === null) {
      nav("/");
    }
  }, [nav, state]);

  // 
  const paymentSeccess = (e, payment) => {
    if (nameRef.current.value < 1) {
      nameRef.current.focus();
      return alert(" .");
    }

    if (phoneRef.current.value < 1) {
      phoneRef.current.focus();
      return alert(" .");
    }

    if (searchAddress.length < 1) {
      return alert(" .");
    }

    if (addressRef.current.value < 1) {
      addressRef.current.focus();
      return alert("  .");
    }

    setLoading(true);

    //       
    if (state.cart === true) {
      const option = [];
      state.product.forEach((data) => option.push(data._id));
      removeCart({ productId: option, userId: userId });
    }

    const option = {
      userId: userId,
      shippingInfo: {
        name: nameRef.current.value,
        phone: phoneRef.current.value,
        address: addressRef.current.value,
        searchAddress: searchAddress,
        req: reqRef.current.value,
      },
      product: state.product,
      payment: payment && payment,
      price: state.totalPrice,
      date: new Date().getTime(),
    };

    userSuccessBuy(option);
    addBuyer(option);
    productsold();

    setTimeout(() => {
      setLoading(false);
      nav("/paymentResult", {
        state: option,
      });
    }, 2000);
  };

  //   
  const productsold = () => {
    const option = [];
    state.product.forEach((data) => {
      option.push({
        id: data._id,
        purchasesCount: data.purchasesCount,
      });
    });
    productSuccessBuy(option);
  };

  return (
    <div className="page">
      {loading === true ? (
        <CheckOutLoading>
          <LoadingOutlined /> .
        </CheckOutLoading>
      ) : null}

      {state && (
        <>
          <SlideAnimation>
            <Procedure>
               &gt; <strong></strong> &gt; 
            </Procedure>
          </SlideAnimation>

          <FadeAnimation>
            <Div>
              <Title> {state.product.length}</Title>
              <hr />
              <CheckOutProduct state={state} />
            </Div>
          </FadeAnimation>

          <FadeAnimation>
            <Div>
              <Title></Title>
              <hr />
              <CheckOutInput
                ref={{
                  nameRef: nameRef,
                  phoneRef: phoneRef,
                  addressRef: addressRef,
                  reqRef: reqRef,
                }}
                searchAddress={searchAddress}
                setSearchAddress={setSearchAddress}
              />
            </Div>
          </FadeAnimation>

          <Div>
            <Title></Title>
            <hr />

            <Carousel1
              height={"200px"}
              point={true}
              slide={true}
              nextBtn={true}
            >
              <CheckOutDiv>
                <PurchaseBtn onClick={paymentSeccess}></PurchaseBtn>
              </CheckOutDiv>

              <CheckOutDiv>
                <PaymentBtn
                  price={state.totalPrice}
                  paymentSeccess={paymentSeccess}
                />
              </CheckOutDiv>
            </Carousel1>

            <ToTalPrice>{`  ${state.totalPrice.toLocaleString()}`}</ToTalPrice>
          </Div>
        </>
      )}
    </div>
  );
}

const Div = styled.div`
  margin-bottom: 2.5rem;
`;

const Title = styled.div`
  font-size: 1.5rem;
  font-weight: 600;
  padding: 1rem;
`;

const Procedure = styled.div`
  font-size: 1rem;
  padding: 0.5rem;
`;

const PurchaseBtn = styled.div`
  display: flex;
  justify-content: center;
  font-size: 1.1rem;
  color: var(--red);
  background-color: var(--white);
  border-radius: 5px;
  border: 2px solid var(--orange_normal);
  padding: 0.3rem;
  width: 70%;

  &:hover {
    cursor: pointer;
    border: 2px solid var(--orange_hover);
  }
`;

const ToTalPrice = styled.div`
  display: flex;
  justify-content: center;
  padding: 2rem;
  width: 100%;
  font-size: 1.5rem;
  font-weight: 700;
`;

const CheckOutLoading = styled.div`
  width: 100%;
  height: 100vh;
  z-index: 100;
  top: 0;
  right: 0;
  position: fixed;

  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  font-size: 2rem;
  font-weight: 600;

  backdrop-filter: blur(5px);
`;

const CheckOutDiv = styled.div`
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
`;

export default React.memo(CheckOut);
