import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import styled from "styled-components";
import { WarningOutlined } from "@ant-design/icons";

import BuyerList from "./BuyerList";
import Loading from "../../Components/Utility/Loading";
import Empty from "../../Components/Utility/Empty";
import ZoomAnimation from "../../Components/Utility/Animation/ZoomAnimation";
import FadeAnimation from "../../Components/Utility/Animation/FadeAnimation";
import SlideAnimation from "../../Components/Utility/Animation/SlideAnimation";


import useAxios from "../../hooks/useAxios";
import useTheme from "../../hooks/useTheme";

import { postUrl } from "../../PostUrl";
import { Image } from "../../Components/Style/ProductCard";

function ProductManagement({ isAuth, userId }) {
  const nav = useNavigate();
  const { darkMode } = useTheme(); // 
  const [totalSold, setTotalSold] = useState(); // 
  const [totalPrice, setTotalPrice] = useState(); // 
  const [loading, setLoading] = useState(true); // 

  //  
  const { resData, connectServer } = useAxios("/api/product/myProduct");
  const { connectServer: getBuyerList, resData: buyerList } = useAxios(
    "/api/user/buyerList"
  );

  useEffect(() => {
    //   
    if (isAuth) {
      connectServer({ id: userId });
      getBuyerList({ id: userId });
    }

    const titleName = document.getElementsByTagName("title")[0];
    titleName.innerHTML = ``;
  }, [isAuth, userId, connectServer, getBuyerList]);

  useEffect(() => {
    //   
    const calcData = (data) => {
      //    
      const sold = data.map((item) => item.sold);

      //    
      const price = data.map((item) => item.sold * item.price);

      if (sold.length === 0) {
        setTotalSold(0);
      }

      if (price.length === 0) {
        setTotalPrice(0);
      }

      if (sold.length > 0) {
        setTotalSold(sold.reduce((prev, current) => prev + current));
      }

      if (price.length > 0) {
        setTotalPrice(price.reduce((prev, current) => prev + current));
      }
    };

    //         
    if (resData && !totalSold && !totalPrice) {
      calcData(resData);
    }

    //   
    if (
      totalSold !== undefined &&
      totalPrice !== undefined &&
      buyerList !== undefined
    ) {
      setLoading(false);
    }
  }, [resData, totalSold, totalPrice, buyerList]);

  return (
    <div className="page">
      {loading ? (
        <Loading />
      ) : (
        <>
          <SlideAnimation>
            <Info>
              <Value>
                <div></div>
                <div>{resData.length}</div>
              </Value>

              <Value>
                <div></div>
                <div>{totalSold}</div>
              </Value>

              <Value>
                <div></div>
                <div>{totalPrice.toLocaleString()}</div>
              </Value>
            </Info>
          </SlideAnimation>

          {/* <FadeAnimation>
            <BuyerList buyerList={buyerList} />
          </FadeAnimation> */}

          {resData.length === 0 ? (
            <Empty>
              <WarningOutlined />
                .
            </Empty>
          ) : (
            <List>
              {resData.map((data, index) => (
                <Card key={index} onClick={() => nav(`/product/${data._id}`)}>
                  <FadeAnimation>
                    <Product darkMode={darkMode}>
                      <NewImage img={`url('${postUrl}${data.image[0]}')`} />
                      <Content>
                        <div>{data.title}</div>

                        <ul>
                          <li>{` ${new Date(
                            data.createdAt
                          ).getFullYear()}. ${
                            new Date(data.createdAt).getMonth() + 1
                          }. ${new Date(data.createdAt).getDate()}`}</li>
                          <li>{` ${data.price.toLocaleString()}`}</li>
                          <li>{` ${data.sold}`}</li>
                          <li>{` ${parseInt(
                            data.price * data.sold,
                            10
                          ).toLocaleString()}`}</li>
                        </ul>
                      </Content>
                    </Product>
                  </FadeAnimation>
                </Card>
              ))}
            </List>
          )}
        </>
      )}
    </div>
  );
}

const Info = styled.div`
  width: 100%;
  height: 6rem;
  font-size: 1rem;
  display: flex;
  justify-content: space-between;
`;

const Value = styled.div`
  background-color: var(--orange_normal);
  width: 33%;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  & > :last-child {
    font-weight: 600;
    font-size: 16px;
  }
`;

const List = styled.div`
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  margin: auto;
`;

//    
const Card = styled.div`
  height: 15rem;
  width: 50%;
  display: flex;
  justify-content: flex-start;

  @media (max-width: 900px) {
    width: 100%;
  }

  //       
  & > :first-child {
    width: 100%;
    display: flex;
  }
`;

//        
//flex-start     
const Product = styled.div`
  background-color: ${(props) =>
    props.darkMode ? "var(--black)" : "var(--white)"};
  box-shadow: 1px 1px 3px 1px var(--gray_transparency);
  width: 95%;
  height: 95%;
  padding: 0.5rem;
  margin: auto;
  border-radius: 5px;
  display: flex;
  border: 3px solid transparent;

  &:hover {
    cursor: pointer;
    border: 3px solid var(--orange_hover);
    transition: all ease 0.3s;
  }
`;

const NewImage = styled(Image)`
  min-width: 30%;
  border-radius: 5px;
  margin-right: 1rem;
`;

const Content = styled.div`
  font-size: 1rem;

  //
  & > :first-child {
    font-weight: 600;
    margin-bottom: 0.5rem;
  }

  //
  & > ul {
    padding-left: 1.5rem;
  }
`;

export default ProductManagement;
