import React, { useEffect } from "react";
import { useLocation } from "react-router";
import { useNavigate } from "react-router-dom";
import styled from "styled-components";

import { Section } from "../../Components/Style/InputStyle";

function PaymentResult() {
  const nav = useNavigate();

  //checkOut  
  const { state } = useLocation();

  useEffect(() => {
    //checkOut    state 
    //    
    if (state === null) {
      nav("/");
    }
    const titleName = document.getElementsByTagName("title")[0];
    titleName.innerHTML = ``;
  }, [nav, state]);

  return (
    <div className="page">
      {state && (
        <>
          <Procedure>
             &gt;  &gt; <strong></strong>
          </Procedure>

          <ToTalPrice>{`  ${parseInt(
            state.price,
            10
          ).toLocaleString()}`}</ToTalPrice>

          <PageBtn>
            <button onClick={() => nav("/purchaseHistory")}>
               
            </button>
            <button onClick={() => nav("/")}> </button>
          </PageBtn>

          <Title> </Title>
          <hr />

          <Section>
            <div></div>
            <div>{state.shippingInfo.name}</div>
          </Section>

          <Section>
            <div></div>
            <div>{state.shippingInfo.phone}</div>
          </Section>

          <Section>
            <div></div>
            <div>{`${state.shippingInfo.searchAddress} ${state.shippingInfo.address}`}</div>
          </Section>

          <Section>
            <div></div>
            <div>
              {state.shippingInfo.req.length === 0
                ? ""
                : state.shippingInfo.req}
            </div>
          </Section>
        </>
      )}
    </div>
  );
}

const Title = styled.div`
  font-size: 1.5rem;
  font-weight: 600;
  padding: 0.5rem;
`;

const Procedure = styled.div`
  font-size: 1rem;
  padding: 0.5rem;
`;

const ToTalPrice = styled.div`
  display: flex;
  justify-content: center;
  padding: 2rem;
  width: 100%;
  font-size: 1.5rem;
  font-weight: 700;
`;

const PageBtn = styled.div`
  width: 100%;
  display: flex;
  justify-content: center;

  & > :first-child {
    margin-right: 10px;
  }

  button {
    padding: 10px;
    border: 2px solid var(--gray);
    background-color: transparent;
    border-radius: 5px;
    width: 150px;
    font-size: 1rem;
    margin-bottom: 1.2rem;

    &:hover {
      border: 2px solid var(--orange_hover);
      cursor: pointer;
    }
  }
`;

export default PaymentResult;
