import React from "react";
import styled from "styled-components";

import { Section, Input } from "../../Components/Style/InputStyle";
import AddressSearch from "./AddressSearch";

import useTheme from "../../hooks/useTheme";

const CheckOutInput = ({ searchAddress, setSearchAddress }, ref) => {
  const { darkMode } = useTheme();
  return (
    <>
      <Section>
        <div></div>
        <Input
          ref={ref.nameRef}
          inputType="name"
          placeholder="  ."
          darkMode={darkMode}
        />
      </Section>

      <Section>
        <div></div>
        <Input
          ref={ref.phoneRef}
          inputType="phone"
          onInput={(e) =>
            (e.target.value = e.target.value.replace(/[^0-9-]/g, ""))
          }
          placeholder="  ."
          darkMode={darkMode}
        />
      </Section>

      <Section>
        <div></div>
        <AddressBox>
          <AddressSearch
            searchAddress={searchAddress}
            setSearchAddress={setSearchAddress}
            darkMode={darkMode}
          />
          <Input
            ref={ref.addressRef}
            inputType="address"
            placeholder="   ."
            darkMode={darkMode}
          />
        </AddressBox>
      </Section>

      <Section>
        <div></div>
        <Input
          ref={ref.reqRef}
          inputType="req"
          placeholder="  ."
          maxLength={40}
          darkMode={darkMode}
        />
        {/* <span>{`${ShippingInfo.req.length} / 40`}</span> */}
      </Section>
    </>
  );
};

const AddressBox = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
`;

export default React.memo(React.forwardRef(CheckOutInput));
//forwardRef     ref props  
