import React, { useState } from "react";
import styled from "styled-components";
import { useSelector } from "react-redux";

import SideMenu from "./SideMenu";
import { HeaderBtn, HeaderLogInBtn } from "./HeaderBtn";
import Notification from "../Utility/Notification";

function Header() {
  //   
  const notification = useSelector((state) => state.notification.notification);

  // 800  
  const [checkSideMenu, setCheckSideMenu] = useState(
    window.innerWidth >= 800 ? false : true
  );

  //   800  sideMenu 
  //    state    
  //boolean   state     
  window.onresize = () => {
    if (window.innerWidth <= 800) {
      setCheckSideMenu(true);
    } else {
      setCheckSideMenu(false);
    }
  };

  return (
    <>
      {notification && <Notification notification={notification} />}

      <HeaderDiv>
        {/*    */}
        <HeaderSection>
          <HeaderLogo></HeaderLogo>
          {!checkSideMenu && <HeaderBtn />}
        </HeaderSection>

        {/*    */}
        <HeaderSection>
          {checkSideMenu ? <SideMenu /> : <HeaderLogInBtn />}
        </HeaderSection>
      </HeaderDiv>
    </>
  );
}

const HeaderDiv = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;

  position: sticky;
  backdrop-filter: blur(20px);
  padding: 1rem;
  border-bottom: 2px solid var(--orange_normal);

  top: 0;
  width: 100%;
  height: 3rem;
  z-index: 20;
`;

const HeaderLogo = styled.span`
  padding-right: 10px;
  font-size: 2rem;
`;

const HeaderSection = styled.div`
  display: flex;
  align-items: center;
`;

export default Header;
