import React, { useState, useEffect } from "react";
import styled from "styled-components";
import { useDispatch } from "react-redux";

import { warningMessageAction } from "../../../store/reducer/warningMessage";
import LoginForm from "./LoginForm";

function Login({ setOpenModal }) {
  const dispatch = useDispatch();
  const [type, setType] = useState("login");

  // > ,  >     
  useEffect(() => {
    dispatch(
      warningMessageAction.setRegisterError({
        error: false,
        message: "",
      })
    );

    dispatch(
      warningMessageAction.setLoginError({
        error: false,
        message: "",
      })
    );
  }, [dispatch, type]);

  // 
  const onLogin = () => {
    setType("login");
  };

  // 
  const onRegister = () => {
    setType("register");
  };

  return (
    <>
      <LoginTitle type={type}>
        {type === "login" ? "" : ""}
      </LoginTitle>

      <LoginForm type={type} setType={setType} setOpenModal={setOpenModal} />

      <div>
        {type === "login" ? (
          <LoginFooter>
             ?
            <span onClick={onRegister}> </span>
          </LoginFooter>
        ) : (
          <LoginFooter>
             ?
            <span onClick={onLogin}></span>
          </LoginFooter>
        )}
      </div>
    </>
  );
}

const LoginTitle = styled.div`
  padding: 1rem;
  font-size: 1.3rem;
  background-color: ${(props) =>
    props.type === "login" ? "var(--orange_normal)" : "var(--green)"};
  margin-bottom: 0.4rem;
`;

const LoginFooter = styled.div`
  font-size: 0.8rem;
  color: gray;
  margin-bottom: 1rem;

  display: flex;
  justify-content: center;

  span {
    margin-left: 0.5rem;
    border-bottom: 1px solid transparent;
    color: red;
    cursor: pointer;

    &:hover {
      border-bottom: 1px solid red;
    }
  }
`;

export default Login;
